"use client";
import React, { useState, useCallback } from 'react';
import JsonView from '@uiw/react-json-view';

// It's assumed that the custom CSS for .glow-border and .glow-on-hover
// (as provided in the previous turn) is available in your global stylesheet.

export default function Testcases() {
  // State for each form field
  const [endpoint, setEndpoint] = useState('');
  const [method, setMethod] = useState('');
  const [expectedStatus, setExpectedStatus] = useState('');
  const [headers, setHeaders] = useState({}); // Initialize as empty object for JSON
  const [requestBody, setRequestBody] = useState({}); // Initialize as empty object for JSON
  const [expectedResponseBody, setExpectedResponseBody] = useState({}); // Initialize as empty object for JSON
  const [expectedHeaders, setExpectedHeaders] = useState({}); // Initialize as empty object for JSON

  // Handler for standard input fields
  const handleInputChange = useCallback((setter) => (e) => {
    setter(e.target.value);
  }, []);

  // Handler for JsonView component changes
  // The onEdit prop of @uiw/react-json-view provides an object with 'json' (parsed object) and 'value' (JSON string)
  const handleJsonViewChange = useCallback((setter) => ({ json }) => {
    setter(json);
  }, []);

  // Form submission handler
  const handleSubmit = useCallback((e) => {
    e.preventDefault(); // Prevent default form submission behavior

    // Construct the test case object
    const testCase = {
      endpoint,
      method,
      expectedStatus: expectedStatus ? parseInt(expectedStatus, 10) : undefined, // Parse to number
      headers,
      requestBody,
      expectedResponseBody,
      expectedHeaders,
    };

    // Log the test case data to the console (you would typically send this to an API)
    console.log('Test Case Data:', testCase);

    // You can add logic here to send this data to your backend API,
    // display a success message, or clear the form.
    alert('Test case created! Check console for data.'); // Using alert for demonstration, replace with custom UI
    // For a real app, replace alert() with a custom modal or toast notification
    // Example: showCustomModal('Test case created successfully!');

    // Optionally, clear the form after submission
    // setEndpoint('');
    // setMethod('');
    // setExpectedStatus('');
    // setHeaders({});
    // setRequestBody({});
    // setExpectedResponseBody({});
    // setExpectedHeaders({});

  }, [endpoint, method, expectedStatus, headers, requestBody, expectedResponseBody, expectedHeaders]);

  return (
    <div className="flex flex-col items-center justify-center min-h-screen p-8 bg-black text-gray-100 font-inter">
      {/* Main Container for the form, centered on the screen */}
      <form
        onSubmit={handleSubmit}
        className="space-y-6 p-8 bg-gradient-to-br from-black to-gray-950 rounded-lg shadow-2xl border border-red-900/40 relative overflow-hidden max-w-2xl w-full"
      >
        {/* Background Glow Overlay */}
        {/* This div creates a subtle radial glow effect behind the form content */}
        <div
          className="absolute inset-0 z-0 opacity-20 pointer-events-none"
          style={{ background: 'radial-gradient(circle at center, rgba(255, 0, 0, 0.1), transparent 70%)' }}
        ></div>

        {/* Endpoint Input */}
        <div className="relative z-10"> {/* Ensure content is above the glow overlay */}
          <label htmlFor="endpoint-url" className="sr-only">Endpoint URL</label>
          <input
            id="endpoint-url"
            type="text"
            placeholder="Endpoint URL"
            value={endpoint}
            onChange={handleInputChange(setEndpoint)}
            className="w-full rounded-md p-3 bg-gray-900 text-gray-100 border border-red-700 focus:border-red-500 focus:ring focus:ring-red-500 focus:ring-opacity-40 transition duration-300 ease-in-out placeholder-gray-500 text-lg glow-border"
            required
          />
        </div>

        {/* Method + Status Code Inputs */}
        <div className="flex flex-col sm:flex-row gap-5 justify-between w-full relative z-10">
          {/* HTTP Method Select */}
          <div className="w-full sm:w-1/2">
            <label htmlFor="http-method" className="sr-only">Select HTTP Method</label>
            <select
              id="http-method"
              value={method}
              onChange={handleInputChange(setMethod)}
              className="w-full rounded-md p-3 bg-gray-900 text-gray-100 border border-red-700 focus:border-red-500 focus:ring focus:ring-red-500 focus:ring-opacity-40 transition duration-300 ease-in-out appearance-none cursor-pointer text-lg glow-border"
              required
            >
              <option value="" className="bg-gray-800 text-gray-400">Select HTTP Method</option>
              <option value="GET" className="bg-gray-800 text-gray-200">GET</option>
              <option value="POST" className="bg-gray-800 text-gray-200">POST</option>
              <option value="PUT" className="bg-gray-800 text-gray-200">PUT</option>
              <option value="DELETE" className="bg-gray-800 text-gray-200">DELETE</option>
              <option value="PATCH" className="bg-gray-800 text-gray-200">PATCH</option>
            </select>
          </div>
          {/* Expected Status Code Input */}
          <div className="w-full sm:w-1/2">
            <label htmlFor="expected-status" className="sr-only">Expected Status Code</label>
            <input
              id="expected-status"
              type="number"
              placeholder="Expected Status Code (e.g., 200)"
              value={expectedStatus}
              onChange={handleInputChange(setExpectedStatus)}
              className="w-full rounded-md p-3 bg-gray-900 text-gray-100 border border-red-700 focus:border-red-500 focus:ring focus:ring-red-500 focus:ring-opacity-40 transition duration-300 ease-in-out placeholder-gray-500 text-lg glow-border"
            />
          </div>
        </div>

        {/* Headers JSON Input */}
        <div className="relative z-10">
          <label htmlFor="headers-json" className="text-white font-semibold mb-2 block">Headers (JSON)</label>
          <JsonView
            id="headers-json"
            value={headers} // Pass the state variable
            onEdit={handleJsonViewChange(setHeaders)} // Handle changes
            contentEditable
            className="w-full min-h-[100px] rounded-md p-3 bg-gray-900 text-gray-100 border border-red-700 focus:border-red-500 focus:ring focus:ring-red-500 focus:ring-opacity-40 transition duration-300 ease-in-out glow-border"
            // Ensure JsonView's internal theme matches
            style={{ backgroundColor: 'transparent', color: '#e5e7eb' }} // Override specific internal styles
          />
        </div>

        {/* Request Body JSON Input */}
        <div className="relative z-10">
          <label htmlFor="request-body-json" className="text-white font-semibold mb-2 block">Request Body (JSON)</label>
          <JsonView
            id="request-body-json"
            value={requestBody}
            onEdit={handleJsonViewChange(setRequestBody)}
            contentEditable
            className="w-full min-h-[100px] rounded-md p-3 bg-gray-900 text-gray-100 border border-red-700 focus:border-red-500 focus:ring focus:ring-red-500 focus:ring-opacity-40 transition duration-300 ease-in-out glow-border"
            style={{ backgroundColor: 'transparent', color: '#e5e7eb' }}
          />
        </div>

        {/* Expected Response Body JSON Input */}
        <div className="relative z-10">
          <label htmlFor="expected-response-body-json" className="text-white font-semibold mb-2 block">Expected Response Body (JSON)</label>
          <JsonView
            id="expected-response-body-json"
            value={expectedResponseBody}
            onEdit={handleJsonViewChange(setExpectedResponseBody)}
            contentEditable
            className="w-full min-h-[100px] rounded-md p-3 bg-gray-900 text-gray-100 border border-red-700 focus:border-red-500 focus:ring focus:ring-red-500 focus:ring-opacity-40 transition duration-300 ease-in-out glow-border"
            style={{ backgroundColor: 'transparent', color: '#e5e7eb' }}
          />
        </div>

        {/* Expected Headers JSON Input */}
        <div className="relative z-10">
          <label htmlFor="expected-headers-json" className="text-white font-semibold mb-2 block">Expected Headers (JSON)</label>
          <JsonView
            id="expected-headers-json"
            value={expectedHeaders}
            onEdit={handleJsonViewChange(setExpectedHeaders)}
            contentEditable
            className="w-full min-h-[100px] rounded-md p-3 bg-gray-900 text-gray-100 border border-red-700 focus:border-red-500 focus:ring focus:ring-red-500 focus:ring-opacity-40 transition duration-300 ease-in-out glow-border"
            style={{ backgroundColor: 'transparent', color: '#e5e7eb' }}
          />
        </div>

        {/* Submit Button */}
        <button
          type="submit"
          className="w-full py-3 bg-gradient-to-r from-red-800 to-red-600 text-white rounded-full hover:from-red-700 hover:to-red-500 focus:outline-none focus:ring-2 focus:ring-red-500 focus:ring-opacity-75 transition duration-300 ease-in-out text-xl font-bold tracking-wide transform hover:scale-105 active:scale-100 glow-on-hover relative z-10"
        >
          Create Test Case
        </button>
      </form>
    </div>
  );
}